import React from 'react';
import PropTypes from 'prop-types';

import { Button, FloatingActions } from '@mssgme/ui';
import { useModalThemes } from '../../hooks';
import { ThemedButton } from '../../UI';

import styles from './FormFooter.scss';

FormFooter.propTypes = {
    ...Button.propTypes,
    contour: PropTypes.bool,
    contentAlignLeft: PropTypes.bool,
};

export function FormFooter(props) {
    const { contentStyle } = useModalThemes();

    return (
        <FloatingActions padded className={styles.root}>
            <ThemedButton fullWidth theme={contentStyle} type="submit" {...props} />
        </FloatingActions>
    );
}
