import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useAsyncFn } from 'react-use';

import { __t } from '@mssgme/shared';
import { useNavigationUtils } from '@mssgme/hooks';
import { LandingsPropTypes } from '@mssgme/ui';
import { useCoreContext, useFrontAPI } from '../../hooks';
import { OrderItemsList } from '../OrderItem';
import { printPrice } from '../printPrice';
import { FormFooter } from '../FormFooter';
import { Row } from './Row';

ShoppingCart.propTypes = {
    order: LandingsPropTypes.order.isRequired,
    onClose: PropTypes.func.isRequired,
};

export function ShoppingCart({ order, onClose }) {
    const { router } = useCoreContext();
    const { generateUrl } = useNavigationUtils();
    const api = useFrontAPI();
    const orderId = order._id;
    const hasItems = order.items.length;

    const [{ loading }, handleModifyItems] = useAsyncFn(
        /** @param {{ productId: string, quantity: number }[]} items Order items */
        async (items) => await api.orders.modify({
            orderId,
            payload: {
                items,
            },
        }),
        [orderId]
    );

    useEffect(() => {
        if (order.finalized) {
            return;
        }

        const { total: { amount, currency }, items } = order;

        document.dispatchEvent(new CustomEvent('openCart', {
            detail: {
                currency,
                value: amount,
                items: items.map(({ name, _id, price: { amount }, quantity }) =>
                    ({
                        name,
                        id: _id,
                        price: amount,
                        quantity,
                    })
                ),
            },
        }));
    }, []);

    if (order.finalized) {
        return <Redirect to={router.orders.toStatus(order._id, order.status)} />;
    }

    return (
        <div>
            <OrderItemsList order={order} onAdjust={order.finalized ? undefined : handleModifyItems} />
            <Row label={__t('total_to_pay')} value={printPrice(order.total)} />

            <FormFooter
                isFetching={loading}
                {...(hasItems ? {
                    tag: Link,
                    to: generateUrl('checkout'),
                    children: __t('make_order'),
                } : {
                    onClick: onClose,
                    children: __t('close'),
                })}
            />
        </div>
    );
}
