import React from 'react';
import PropTypes from 'prop-types';
import { Redirect, useLocation } from 'react-router-dom';

import { __t } from '@mssgme/shared';
import { Spinner } from '@mssgme/ui';
import { useCoreContext } from '../../hooks';
import { ThemedModal } from '../../UI';
import { useOrder } from '../useOrder';
import { CheckoutForm } from './CheckoutForm';

import styles from './CheckoutPage.scss';

export const CheckoutPage = ({ orderId }) => {
    const { state } = useLocation();
    const { router } = useCoreContext();
    const { order, loading, error, isNotFound } = useOrder(orderId);
    const backPath = state?.backPath || router.orders.toView(orderId);

    if (!(loading || error || order)) {
        return null;
    }

    const title = order?.finalized ? `${__t('order_term')} #${order.feedback.order}` : __t('checkout');
    const renderContent = () => {
        if (loading) {
            return <Spinner />;
        }

        if (error) {
            return (
                <div className={styles.error}>
                    {isNotFound ? __t('order_not_found') : error.message}
                </div>
            );
        }

        if (order.finalized) {
            return <Redirect to={router.orders.toStatus(order._id, order.status)} />;
        }

        return (
            <CheckoutForm order={order} />
        );
    };

    return (
        <ThemedModal overlay compact padding title={title} onClose={backPath}>
            {renderContent}
        </ThemedModal>
    );
};

CheckoutPage.propTypes = {
    orderId: PropTypes.string.isRequired,
};
