import React from 'react';
import PropTypes from 'prop-types';

import { cls } from '@mssgme/helpers';
import { FeaturesBlockKind } from '@mssgme/shared';
import { useBlockTheme } from '../../../hooks';

import styles from './OrderView.module.scss';

export const Section = ({ title, ...rest }) => {
    const { style } = useBlockTheme({ kind: FeaturesBlockKind });

    return (
        <>
            {title && <h3 className={styles.sectionHeading}>{title}</h3>}
            <div style={style} {...cls(styles.pane, rest)} />
        </>
    );
};

Section.propTypes = {
    title: PropTypes.any,
};
